<template>
  <div id="app">
    {{msg}}
    <h2>Кількість переходів</h2>
    <div id="chartContainer">
      <canvas id="myChart" width="400" height="100"></canvas>
    </div>
    <div id="chartContainer2">
      <div class="chartCountry">
        <span class="mdi mdi-desktop"></span>
        <h2>Країни</h2>
        <canvas id="countryPie" width="400" height="100"></canvas>
        <div id="countryData"></div>
      </div>
      <div class="chartDevice">
        <h2>Девайси</h2>
        <canvas id="devicePie" width="400" height="100"></canvas>
        <div id="deviceData"></div>
      </div>
    </div>
    <div id="picker">
      <vue-date-picker v-model="startDate.value" @update:model-value="handleDate" ref="datePicker1" @input="onStartDateSelected" :date="selectedDateFrom" :format="yyyy-MM-dd"></vue-date-picker>
      <vue-date-picker v-model="endDate.value" ref="datePicker2" @input="onEndDateSelected" :date="selectedDateTo" :format="yyyy-MM-dd" ></vue-date-picker>
      <button class="custom-button" @click="onOkClick">Ок</button>
    </div>
    <div class="tableContainer">
      <table id="clickTable" class="custom-table">
    <thead>
        <tr>
            <th>Країна</th>
            <th>Провайдер</th>
            <th>ОС</th>
            <th>Браузер</th>
            <th>Девайс</th>
            <th>Реферер</th>
            <th>Час кліку</th>
        </tr>
    </thead>
    <tbody>
    </tbody>
</table>
