  </div>


</template>
<script src="https://code.jquery.com/jquery-3.6.0.min.js"></script>
<script src="https://cdn.jsdelivr.net/npm/chart.js"></script>
<script src="https://cdn.jsdelivr.net/npm/axios/dist/axios.min.js"></script>
<script src="https://cdn.datatables.net/1.10.24/js/jquery.dataTables.js"></script>


<script>
import Chart from 'chart.js/auto';
import axios from 'axios';
import $ from 'jquery';
import 'datatables.net';
import 'datatables.net-bs4/css/dataTables.bootstrap4.css';
import 'datatables.net-bs4/js/dataTables.bootstrap4';
import 'flag-icon-css/css/flag-icons.css';
import VueDatePicker from 'vue2-datepicker';
import moment from 'moment';
import 'vue2-datepicker/index.css';
import 'vue2-datepicker/locale/es/uk';
import '@mdi/font/css/materialdesignicons.css';
export default {
  name: 'App',
  components:{
    VueDatePicker
  },
  props:{
  msg:String
  },
  data(){
    return {
      selectedDateFrom: { value: null }, // Добавьте свойство "selectedDate" в data
      startDate: { value: null },
      endDate: { value: null },
      selectedDateTo: { value: null },
      datePickerFormat: 'yyyy-MM-dd',
      myChart: null,
      countryPie: null,
      devicePie: null,
      table: null,
      dataTable: null,
    };
  },
  mounted() {
    //const today = new Date();
    //const sevenDaysAgo = new Date(today.getTime() - 7 * 24 * 60 * 60 * 1000);
    //this.startDate.value = sevenDaysAgo;
    //this.endDate.value = today;
    moment.locale('ua');
    this.showData()
  },
  methods: {

    showData(){



      const mainCtx = document.getElementById('myChart');
      const countryCtx = document.getElementById('countryPie');
      const deviceCtx = document.getElementById('devicePie');
      const url = new URL(window.location.href);
      const linkName = url.searchParams.get('link');
      const dateFrom = url.searchParams.get('date_from');
      const dateTo = url.searchParams.get('date_to');
      this.startDate.value = new Date(dateFrom);
      this.endDate.value = new Date(dateTo);
      const encodedStartDate = encodeURIComponent(dateFrom);
      const encodedEndDate = encodeURIComponent(dateTo);
      this.table = $('#clickTable');
      
      
      
      axios.get('http://127.0.0.1:8000/api/v1/stat/', {
      params: {
        link: linkName,
        date_from: encodedStartDate,
        date_to: encodedEndDate
      }
    })
        .then(response => {
          console.log(response.data)
          const chartData = response.data;
          if (this.myChart) {
            this.myChart.destroy(); // Уничтожаем предыдущий экземпляр графика
          }
          this.myChart = new Chart(mainCtx, {
            type: 'bar',
            data: {
              labels: chartData.labels,
              datasets: [{
                label: "Кліки",
                data: chartData.clicks_count,
                borderWidth: 1
              }]
            },
            options: {
              scales: {
                y: {
                  beginAtZero: true,
                  
                },
                
              }
            }
          });

          if (this.countryPie) {
            this.countryPie.destroy(); // Уничтожаем предыдущий экземпляр графика
          }
        this.countryPie =  new Chart(countryCtx, {
        type: 'pie',
        data: {
          labels: chartData.country_labels,
          datasets: [{
            data: chartData.country_clicks_count,
          }]
        },
        options: {
        
        }
      });
      
      const countryDataElement = document.getElementById('countryData');
      const dataLabels = chartData.country_labels;
      const dataValues = chartData.country_clicks_count;

      let countryDataHTML = '<ul class="country-data">';
      for (let i = 0; i < dataLabels.length; i++) {
        const countryIconClass = 'flag-icon flag-icon-' + dataLabels[i].toLowerCase();
        countryDataHTML += '<li class="country-list-item"><span class="' + countryIconClass + '"></span>' + dataLabels[i] + ': ' + dataValues[i] + '</li>';
      }
      countryDataHTML += '</ul>';

      countryDataElement.innerHTML = countryDataHTML;


      if (this.devicePie) {
            this.devicePie.destroy(); 
          }
      this.devicePie =  new Chart(deviceCtx, {
        type: 'pie',
        data: {
          labels: chartData.device_labels,
          datasets: [{
            data: chartData.device_clicks_count,
          }]
        },
        options: {
          
        }
      });
      const deviceDataElement = document.getElementById('deviceData');
      const dataLabelsDevice = chartData.device_labels;
      const dataValuesDevice = chartData.device_clicks_count;

      let deviceDataHTML = '<ul class="device-data">';
      for (let i = 0; i < dataLabelsDevice.length; i++) {
        const deviceIcon = require('@/assets/' + dataLabelsDevice[i] + '.png'); 
        console.log(deviceIcon);
        deviceDataHTML += '<li class="device-list-item"><i class="' + deviceIcon + '"></i>' + dataLabelsDevice[i] + ': ' + dataValuesDevice[i] + '</li>';
      }
      deviceDataHTML += '</ul>';

      deviceDataElement.innerHTML = deviceDataHTML;


      if (this.dataTable) {
        this.dataTable.destroy(); // Уничтожаем предыдущий экземпляр DataTable
      }
       this.dataTable = this.table.DataTable({
            language: {
              search: 'Пошук:',
              lengthMenu: 'Показати _MENU_ записів',
              info: 'Показано з _START_ по _END_ із _TOTAL_ записів',
              infoEmpty: 'Немає даних для відображення',
              infoFiltered: '(відфільтровано з _MAX_ записів)',
              paginate: {
                first: 'Перша',
                last: 'Остання',
                next: 'Наступна',
                previous: 'Попередня',
              },
            },
            lengthMenu: [10, 25, 50],
            pageLength: 10,
            responsive: true,
          });
      this.fillTableWithData(chartData.clicks);
        })
        .catch(error => {
          console.error(error);
        });
    },

     handleDate (modelData) {
      console.log(modelData)
  date.value = modelData;
},
    onStartDateSelected(date) {
      this.selectedDateFrom.value = date;
    },

    onEndDateSelected(date) {
      this.selectedDateTo = date;
    },


    fetchDataAndRefresh(){

    },

    onOkClick() {
      if (this.startDate && this.endDate) {


        const formatDate = (date) => {
          const formattedDate = moment(date).format('YYYY-MM-DD HH:mm:ss.SSSZ');
          return formattedDate;
      };
        const formattedStartDate = formatDate(this.startDate.value);
        const formattedEndDate = formatDate(this.endDate.value);
        const url = new URL(window.location.href);
        const searchParams = new URLSearchParams(url.search);
        searchParams.set('date_from', formattedStartDate);
        searchParams.set('date_to', formattedEndDate);
        url.search = searchParams.toString();
        window.history.pushState({ path: url.href }, '', url.href);
        this.showData()
        this.fetchDataAndRefresh();

      } else {
        console.log('Пожалуйста, выберите начальную и конечную дату');
      }
      },
    onStartDateSelected(date) {
      this.selectedDateFrom = date;
    },
    onEndDateSelected(date) {
      this.selectedDateTo = date;
    },
    fillTableWithData(data) {
      this.table = $('#clickTable').DataTable();

      console.log("hdsf")
      this.table.clear();

      
      data.forEach(click => {
        const date = new Date(click.time_create);
        const year = date.getFullYear();
        const month = (date.getMonth() + 1).toString().padStart(2, '0');
        const day = date.getDate().toString().padStart(2, '0');
        const hours = date.getHours().toString().padStart(2, '0');
        const minutes = date.getMinutes().toString().padStart(2, '0');
        const seconds = date.getSeconds().toString().padStart(2, '0');
        const countryIcon = `<span class="flag-icon flag-icon-${click.country.toLowerCase()}"></span>`;
        const formattedDate = `${year}-${month}-${day} ${hours}:${minutes}:${seconds}`;
        this.table.row.add([
          `${countryIcon} ${click.country}`,
          click.provider,
          click.os,
          click.browser,
          click.device,
          click.referer,
          formattedDate,
        ]);
      });

      this.table.draw();
    }
  }
};
</script>

